export function searchHtml() {
  $(".header__midl").html(
    `
       <div class="header__search">
          <div class="search" data-search>
            <button class="search__icon btn btn__search" data-toggle-search></button>
            <div class="search__input">
              <input type="text" name="search" placeholder="" data-input-search>
              <button class="search__clear btn btn__clear" data-clear-search></button>
            </div>
          </div>
        </div>
    `
  );
}
