// Dropdown button
export function dropdownButton() {
  $(".dropdown-menu .container").html(
    `
      <div class="dropdown" data-dropdown>
        <button class="dropdown__toggle btn btn__primary" id="dropdownMenuButton" data-dropdown-toggle>Dropdown
          button</button>
        <ul class="dropdown__menu" data-toggle-id="dropdownMenuButton">
          <li class="dropdown__item"><a href="#" class="dropdown__link">Action</a></li>
          <li class="dropdown__item"><a href="#" class="dropdown__link">Action #2</a></li>
          <li class="dropdown__item"><a href="#" class="dropdown__link">Action #3</a></li>
        </ul>
      </div>
    `
  );
}

// Dropdown list-item
export function dropdownListItem() {
  $(".dropdown-menu .container").html(
    `
      <div class="dropdown" data-dropdown>
        <div class="dropdown__wrap">
          <a href="tel:+79998887766" class="dropdown__link">+7 (999)-999-99-99</a>
          <button class="dropdown__toggle btn btn__icon" id="dropdownLink" data-dropdown-toggle><img
              src="./img/sprite/arrow_down_icon.svg" alt=""></button>
        </div>
        <ul class="dropdown__menu" data-toggle-id="dropdownLink">
          <li class="dropdown__item"><a href="tel:+79998887766" class="dropdown__link">+7 (999)-777-66-55</a></li>
          <li class="dropdown__item"><a href="tel:+79998887766" class="dropdown__link">+7 (999)-666-55-44</a></li>
          <li class="dropdown__item"><a href="tel:+79998887766" class="dropdown__link">+7 (999)-555-66-33</a></li>
        </ul>
      </div>
    `
  );
}

// Dropdown menu
export function dropdownMenuItem() {
  console.log($(".dropdown-menu").find(".container"));
  $(".dropdown-menu")
    .find(".container")
    .html(
      `
      <li class="dropdown" data-dropdown>
        <div class="dropdown__wrap">
          <a href="#" class="dropdown__link"></a>
          <button class="dropdown__toggle btn btn__icon" id="dropdownMenuLink" data-dropdown-toggle>
            <img src="./img/arrow_down.svg" alt="arrow-down">
          </button>
        </div>
        <ul class="dropdown__menu" data-toggle-id="dropdownMenuLink">
          <li class="dropdown__item"><a href="#" class="dropdown__link"></a></li>
          <li class="dropdown__item"><a href="#" class="dropdown__link"></a></li>
          <li class="dropdown__item"><a href="#" class="dropdown__link"></a></li>
        </ul>
      </li>
    `
    );
}
